import React, { useEffect, useMemo, useRef, useState } from 'react';

import * as s from './style.module.scss';

function Main() {
  const [width, setWidth] = useState<number>(0);

  const ref = useRef<HTMLElement>(null);
  const observer = useRef<ResizeObserver | null>(null);

  useEffect(() => {
    observer.current = new ResizeObserver(([entry]) => {
      setWidth(entry.contentRect.width);
    });

    observer.current.observe(ref.current!);

    return () => {
      observer.current?.disconnect();
    };
  }, []);

  // TODO(dwchung): Add reveal effect.

  return useMemo(
    () => (
      <main className={s.main} ref={ref}>
        <div className={s.wrap}>
          <p>
            같은 나라와 공간에서 몇번이나 스쳐 지나갔던 인연에서&nbsp;시작해
            {width >= 640 ? <br /> : ' '}
            2020년 비로소 사랑으로 맺어진&nbsp;저희가,
            {width >= 640 ? <br /> : ' '}
            2023년 새해의 길목에서 하나가 되려&nbsp;합니다.
          </p>

          <p>
            이렇게 인생에서 가장 중요하고 소중한&nbsp;날,
            {width >= 640 ? <br /> : ' '}
            저희를 아껴주신 감사한 분들을 모시는 것이 도리인 줄 알고&nbsp;있지만
            {width >= 640 ? <br /> : ' '}
            코로나로 인한 사회적 분위기와 두 사람의 뜻에&nbsp;따라
            {width >= 640 ? <br /> : ' '}
            양가 가족과 형제만 모시고 작은 결혼식을 하게&nbsp;되었습니다.
          </p>

          <p>
            이 즐거운 행사에 함께 모시지 못하는 점 송구스럽게&nbsp;생각하며,
            {width >= 640 ? <br /> : ' '}
            진심어린 마음으로 축하해 주셨으면&nbsp;좋겠습니다.
          </p>

          <p>행복하고 예쁘게 잘&nbsp;살겠습니다.</p>
        </div>
      </main>
    ),
    [width],
  );
}

export default Main;
